import React, { Component } from 'react';
import { Link,hashHistory } from 'react-router';

class Test extends Component {
	componentDidMount(){
		 this.props.router.setRouteLeaveHook(
        this.props.route, 
        this.leave
      )
	}
	leave(nextLocation) {
      //  false 
      // 
        return '';
    }
	sub(ev){
		ev.preventDefault();
		const username=ev.target.elements[0].value;
		console.log(username); //text
		console.log(hashHistory.getCurrentLocation().pathname)// Go/test
		console.log(window.location)//pathnamehash
		const path=`/test/${username}`;
		
		if(path==hashHistory.getCurrentLocation().pathname){ //1
			console.log(1);
		}
		hashHistory.push(path);
	}
	
  render() {
  	
    return (
      <div className="Test">
      <p>test</p>
      <p>text123/test/123'/'</p>
      <Link to='/test/example1' activeClassName="active">qu example1</Link>{'  '}
      <Link to='/test/example2' activeClassName="active">qu example2</Link>{'  '}
      <Link to='/test/example3' activeClassName="active">qu example3</Link>{'  '}
      
      <form onSubmit={this.sub} style={{margin:'10px'}}>
        <input type='text' placeholder='name' style={{marginRight:'10px'}}/>
        <input type='submit' value='Go'/>
      </form>
      
      {
        	this.props.children || 
        	<h2>
        	  example1
        	</h2>
        } 
      </div>
    );
  }
}

export default Test;



